html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #ffffff;
  font-size: 2rem;
  background-color: #0d0a20;
  cursor: cell;
}
body.overflow-hidden {
  overflow: hidden;
  /* Crea problemas con el nav bar en Celular 
  position: fixed;
  width: 100%;
  height: 100%;
  */
}

p {
  line-height: 1.5;
}

.main-container {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .main-container {
    width: 90%;
  }
}

a {
  text-decoration: none;
  color: #ffffff;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 4.6rem;
}

h3 {
  font-size: 6rem;
  text-align: center;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

body > section {
  padding: 10rem 0;
}

.modal {
  z-index: 2;
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: contrast(90%) grayscale(20%) brightness(50%) blur(5px);
  animation: fadeIn ease-in 0.2s;
}
.modal.fade-out {
  animation: fadeOut ease-in 0.5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
