.header__content {
  background-color: rgba(13, 13, 13, 0.1019607843);
  backdrop-filter: blur(4px);
  /*    grayscale(.8) brightness(0.3) blur(3px); */
  /* Aplica el desenfoque detrás del header */
  -webkit-backdrop-filter: blur(10px);
  /* Para compatibilidad con Safari */
  height: 12rem;
  width: 100%;
  position: fixed;
  z-index: 10;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .header__content {
    gap: 50rem;
  }
}
@media (min-width: 768px) {
  .header__content {
    flex-direction: row;
    display: flex;
    justify-content: space-around;
    align-items: baseline;
    border-bottom: 0.15rem dashed #1d1736;
  }
}
.header__content a {
  font-weight: 500;
}
.header__content label {
  transition: filter 0.5s ease-in-out;
}
.header__content label:hover {
  filter: hue-rotate(220deg) !important;
  transform: scale(0.52);
}

.main__nav {
  z-index: 102;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
@media (max-width: 767px) {
  .main__nav {
    gap: 3rem;
  }
}
@media (min-width: 768px) {
  .main__nav {
    flex-direction: row;
    opacity: 1;
    visibility: visible;
  }
}
@media (max-width: 767px) {
  .main__nav a {
    padding: 2rem 3rem;
    text-transform: uppercase;
    border-radius: 8px;
    font-size: 17px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.5019607843);
    text-shadow: none;
    background: transparent;
    cursor: pointer;
    box-shadow: transparent;
    border: 1px solid rgba(255, 255, 255, 0.5019607843);
    transition: 0.5s ease;
    user-select: none;
  }
  .main__nav a:hover, .main__nav a:focus {
    font-weight: 400;
    color: #ffffff;
    background: #00AAAA;
    border: 1px solid #ececb0;
    text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 20px #ffffff;
    box-shadow: 0 0 5px #ececb0, 0 0 20px #ececb0, 0 0 50px #ececb0, 0 0 100px #ececb0;
  }
  .main__nav a:nth-child(1) {
    margin-top: 2rem;
  }
}

/* Nav Links Hover Animation */
.main__nav a {
  position: relative;
  color: #ffffff;
  text-decoration: none;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
.main__nav a:hover {
  transform: scale(1.1);
}

.main__nav a span {
  position: relative;
  display: inline-block;
  transition: transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.main__nav a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #ececb0;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.main__nav a:hover,
.active:not(:nth-of-type(3)) {
  font-weight: 700;
  color: #ececb0 !important;
}

.main__nav a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}

.main__nav a:hover span {
  transform: translateY(-3px);
}

/* Efecto adicional de texto flotante (opcional) */
.main__nav a span::after {
  content: attr(data-text);
  position: absolute;
  top: 110%;
  left: 0;
  color: #ffffff;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.main__nav a:hover span::after {
  opacity: 0.7;
}

.main__nav {
  transition: transform 0.3s ease-in-out;
}

.main__nav.visible {
  transform: translateX(0);
}

.header__title {
  text-align: center;
  color: #ffffff;
  margin-top: 4rem;
  margin-bottom: 0;
  transition: color 0.4s ease-in-out;
}
.header__title:hover {
  color: #e0e2f5;
}
@media (min-width: 768px) {
  .header__title {
    margin-top: 0;
  }
}
.header__title span {
  color: #ececb0;
}

/* CSS para el efecto exacto */
@media (min-width: 768px) {
  .nav-link {
    position: relative;
    color: #ececb0;
    text-decoration: none;
    padding: 4px 0;
    overflow: hidden;
    transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    display: inline-block;
  }
  .nav-link::before {
    content: attr(data-content);
    position: absolute;
    top: 0;
    left: 0;
    color: rgba(236, 236, 176, 0.4);
    filter: blur(3px);
    transform: translateY(120%) scaleY(0.8);
    opacity: 0;
    transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    pointer-events: none;
    width: 100%;
    text-align: center;
  }
  .nav-link:hover {
    transform: translateY(-5px);
    color: #fff;
  }
  .nav-link:hover::before {
    transform: translateY(35%) scaleY(0.9);
    opacity: 1;
    filter: blur(1.2px);
  }
}
/* Subrayado dinámico */
.nav-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: currentColor;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.nav-link:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}

.visible {
  z-index: 101;
  visibility: visible;
  opacity: 1;
  height: auto;
}

/* From Uiverse.io by KSAplay */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  cursor: pointer;
  user-select: none;
  transform: scale(0.5);
}

.simple-text {
  position: absolute;
  bottom: -40px;
  width: 120px;
  text-align: center;
  color: white;
  font-size: 16pt;
  font-weight: 800;
  font-family: monospace;
}

.torch {
  display: flex;
  justify-content: center;
  height: 150px;
}

.head,
.stick {
  position: absolute;
  width: 30px;
  transform-style: preserve-3d;
  transform: rotateX(-30deg) rotateY(45deg);
}

.stick {
  position: relative;
  height: 120px;
}

.face {
  position: absolute;
  transform-style: preserve-3d;
  width: 30px;
  height: 30px;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50%;
  background-color: #000000;
}

.top {
  transform: rotateX(90deg) translateZ(15px);
}

.left {
  transform: rotateY(-90deg) translateZ(15px);
}

.right {
  transform: rotateY(0deg) translateZ(15px);
}

.top div,
.left div,
.right div {
  width: 102%;
  height: 102%;
}

.top div:nth-child(1),
.left div:nth-child(3),
.right div:nth-child(3) {
  background-color: rgba(255, 255, 151, 0.3764705882);
}

.top div:nth-child(2),
.left div:nth-child(1),
.right div:nth-child(1) {
  background-color: rgba(255, 216, 0, 0.3764705882);
}

.top div:nth-child(3),
.left div:nth-child(4),
.right div:nth-child(4) {
  background-color: rgba(255, 255, 255, 0.3764705882);
}

.top div:nth-child(4),
.left div:nth-child(2),
.right div:nth-child(2) {
  background-color: rgba(255, 143, 0, 0.3764705882);
}

.side {
  position: absolute;
  width: 30px;
  height: 120px;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: repeat(8, 12.5%);
  cursor: pointer;
  translate: 0 12px;
}

.side-left {
  transform: rotateY(-90deg) translateZ(15px) translateY(8px);
}

.side-right {
  transform: rotateY(0deg) translateZ(15px) translateY(8px);
}

.side-left div,
.side-right div {
  width: 103%;
