/* From Uiverse.io by Jason21403019 */
.hamburgers {
  background-color: rgba(13, 13, 13, 0.1019607843);
  backdrop-filter: blur(4px);
  margin-top: 3.4rem;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .hamburgers {
    display: none;
  }
}

.hamburger {
  cursor: pointer;
  padding-bottom: 3px;
  position: relative;
}

.overflow-btn {
  z-index: 50;
}

.hamburger input {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
}

.bar {
  display: block;
  width: 30px;
  height: 3px;
  margin: 6px auto;
  border-radius: 40px;
  transition: all 0.3s cubic-bezier(0.37, -1.11, 0.79, 2.02);
  background-color: #ffffff;
}

.hamburger input:checked ~ .bar:nth-child(2) {
  transform: translateY(9.5px) rotate(45deg);
}

.hamburger input:checked ~ .bar:nth-child(3) {
  opacity: 0;
}

.hamburger input:checked ~ .bar:nth-child(4) {
  transform: translateY(-8px) rotate(-45deg);
}

